@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  -ms-overflow-style: none;
}

body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  box-sizing: border-box;
}

.wrapper{display:inline-flex;margin-top:50px}.wrapper_Bottom{position:absolute;bottom:10px}.wrapper .icon{position:relative;background-color:#ffffff;color:#111;border-radius:50%;padding:15px;margin:10px;width:50px;height:50px;font-size:18px;display:flex;justify-content:center;align-items:center;flex-direction:column;box-shadow:0 10px 10px rgba(0,0,0,0.1);cursor:pointer;transition:all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper_Bottom .icon{width:30px;height:30px}a{color:inherit}.wrapper .tooltip{position:absolute;top:0;font-size:14px;padding:5px 8px;border-radius:5px;box-shadow:0 10px 10px rgba(0,0,0,0.1);opacity:0;pointer-events:none;width:150px !important;transition:all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper .tooltip::before{position:absolute;content:"";height:8px;width:8px;background-color:#ffffff;bottom:-3px;left:50%;-webkit-transform:translate(-50%) rotate(45deg);transform:translate(-50%) rotate(45deg);transition:all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper .icon:hover .tooltip{top:-45px;opacity:1;visibility:visible;pointer-events:auto}.wrapper .icon:hover span,.wrapper .icon:hover .tooltip{text-shadow:0px -1px 0px rgba(0,0,0,0.1)}.wrapper .Itroduction:hover,.wrapper .Itroduction:hover .tooltip,.wrapper .Itroduction:hover .tooltip::before{background-color:#d35f32;color:#ffffff}.wrapper .Portfolio:hover,.wrapper .Portfolio:hover .tooltip,.wrapper .Portfolio:hover .tooltip::before{background-color:#2895c4;color:#ffffff}.wrapper .Contact:hover,.wrapper .Contact:hover .tooltip,.wrapper .Contact:hover .tooltip::before{background-color:#e1306c;color:#ffffff}*{margin:0;padding:0;box-sizing:border-box}*:focus,*:active{outline:none !important;-webkit-tap-highlight-color:transparent}html{font-size:calc(10px + 1vmin)}.App{text-align:center}.App-header{background-color:#333f50;min-height:100vh;display:flex;flex-direction:column;align-items:center;align-content:center;justify-content:center;color:white}.divPhoto{position:relative;display:flex;flex-direction:column;align-items:center;justify-content:center}.divPhoto .ImgCircle{width:110%;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.IntroName{font-weight:bolder;font-size:1.5rem;margin-top:20px}.IntroTxt{margin-top:30px}

.divTag{margin:0 auto;width:100%;text-align:left}.divTag .labTag{display:inline-block;text-align:center;font-weight:bolder;background:#b9f1ff;color:#333F50;width:8rem;padding-top:7px;padding-bottom:5px;border-radius:20px 20px 0 0;margin-right:5px;cursor:pointer}.divTag .labTag-selected{padding-top:12px;background:yellow}.MainTxt{background:rgba(255,255,255,0.205);border-radius:0 25px 25px;margin:0 auto;width:70vw;text-align:right;padding:20px 50px;line-height:2rem;min-height:66vh;height:auto;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center;align-content:center;overflow:scroll;z-index:100;position:relative}.MainTxt::-webkit-scrollbar{display:none}.PicDeco{position:absolute;z-index:0}.PicDeco1{left:-5vh;bottom:-5vh;background:#FFC000;width:15vh;height:45vh;-webkit-transform:rotate(30deg);transform:rotate(30deg)}.divIntroduction,.divSkill{width:100%;flex-shrink:0}.divThought{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-around;align-items:center;align-content:center;height:100%;color:#D29500}.divThought h2{text-align:left;line-height:3rem}.divSkill{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between;align-items:stretch;align-content:stretch;height:100%}.divSkill .SkillBlock{width:23%}.divSkill .SkillBlock .SkillTitle{font-size:2rem;font-weight:bolder;text-align:center;padding:40px 0 18px 0}.divSkill .SkillBlock .SkillTitle.SkillTitle0{background:#8497B0}.divSkill .SkillBlock .SkillTitle.SkillTitle1{background:#A9D18E}.divSkill .SkillBlock .SkillTitle.SkillTitle2{background:#F4B183}.divSkill .SkillBlock .SkillTitle.SkillTitle3{background:#E45E74}.divSkill .SkillBlock .SkillList span{color:black;text-align:center;display:block;margin:5px;font-weight:500}

